 ### Customize

 ``` css
  --font-family: 'Source Sans Pro', sans-serif;
  --font-weight: 400;
  --font-weight-bold: 600;
  --h-margin: 1em 0 .25em;
  ```

 */

:root {
  --font-family: 'Source Sans Pro', sans-serif;
  --font-weight: 400;
  --font-weight-bold: 600;
  --h-margin: 0 0 8px;

}

.t-default {
  /* specify the root styles of the document */
  background-color: var(--white);
  box-sizing: border-box;
  color: var(--black);
  cursor: default;
  font: 100%/1.5 sans-serif;
  text-rendering: optimizeLegibility;

  /* specify the background color, font color, and drop shadow of text selections */
  ::selection {
    background-color: #b3d4fc; /* required when declaring ::selection */
    color: #4c2b03;
    text-shadow: none;
  }

  /* specify the text decoration of anchors */
  a { text-decoration: none; }

  /* specify the font family of code elements */
  code,
  kbd,
  pre,
  samp { font-family: monospace, monospace; }

  /* specify the list style of nav lists */
  nav ol,
  nav ul { list-style: none; }

}

/* Lists */

%no-bullet {
  list-style-image: none;
  list-style-type: none;
  margin-left: 0;
}

%no-bullets {
  list-style: none;
  li { @extend %no-bullet; }
}

%list-horizontal {
  @extend %reset-box;
  clear: both;
}

.u-list-inline,
%list-inline {
  @extend %no-bullets;
  margin-bottom: .5em;
  margin-left: .5em;
  margin-top: 0;
  overflow: hidden;
  padding: 0;

  > li {
    display: block;
    float: left;
    margin-left: 1em;

    > * { display: block; }
  }
}

/* Links */

@define-mixin link-colors $normal, $hover, $active, $visited, $focus {
  color: $normal;
  &:visited { color: $visited; }
  &:focus { color: $focus; }
  &:hover { color: $hover; }
  &:active { color: $active; }
}

%link-hover {
  text-decoration: none;
  &:hover,
  &:focus { text-decoration: underline; }
}

@define-mixin link-unstyled {
  color: inherit;
  cursor: inherit;
  text-decoration: inherit;
  &:active,
  &:focus {
    outline: none;
  }
}

/* Overflow */

%ellipsis,
.u-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* Headlines */

%h-base-styles {
  line-height: 1.3;
  margin: var(--h-margin);
}

.t-default {
  font: 16px/2 var(--font-family);
  font-weight: var(--font-weight);

  h1,
  h2,
  h3, {
    @extend %h-base-styles;
    font-weight: var(--font-weight);
  }

  h4,
  h5,
  h6 {
    @extend %h-base-styles;
    font-weight: var(--font-weight-bold);
  }

  h1 { font-size: 48px; }
  h2 { font-size: 40px; }
  h3 { font-size: 31px; }
  h4 { font-size: 25px; }
  h5 { font-size: 20px; }
  h6 { font-size: 16px; }

  a:not([class]) {
    @extend %link-hover;
    color: var(--blue-700);
    text-decoration: none;
  }

  p {
    line-height: 1.4;
    margin-bottom: 1em;
  }

  small {
    display: block;
    font-size: 75%;
    line-height: 1.5;
    margin-bottom: 1em;
  }
}

