@define-mixin menu $background, $hover {
  > ul {
    @extend %row;
    @extend %reset-box;
    background-color: $background;
    color: contrast($background);
    position: relative;

    > li > a {
      color: contrast($background);
      cursor: pointer;
      display: block;
      padding: .8rem 1.5rem;
      transition-duration: var(--global-duration);

      + ul {
        background-color: $background;
        color: contrast($background);
        min-width: 150px;

        a {
          color: contrast($background);
          display: block;
          padding: .8rem 1rem;
          width: 100%;

          &:hover,
          &:focus {
            text-decoration: none;
          }
        }
      }

      &:focus,
      &:active {
        color: var(--gold-300);
      }
    }

    > li:hover > a,
    > li:focus > a {
      background-color: $hover;
      + ul {
        display: block;
        a:hover,
        a:focus {
          background-color: $hover;
        }
      }
    }
  }
}

.c-menu,
%menu { @mixin menu var(--primary-color), var(--blue-700); }

.c-menu {
  ul { display: none; }
}

